/**
 * ScrollHint — toast that tells the user how to zoom.
 * Ported from js-cloudimage-hotspot zoom/scroll-hint.ts to plain JS.
 *
 * Shows "Ctrl + scroll or pinch to zoom" then auto-hides.
 */

const AUTO_HIDE_MS = 1500;

export class ScrollHint {
  /**
   * @param {HTMLElement} container — element to append the toast into
   */
  constructor(container) {
    this._container = container;
    this._el = null;
    this._hideTimer = null;
    this._created = false;
  }

  show() {
    if (!this._created) {
      this._el = document.createElement('div');
      this._el.className = 'cloudimage-360-scroll-hint';
      this._el.setAttribute('role', 'status');
      this._el.setAttribute('aria-live', 'polite');
      this._el.textContent = 'Ctrl + scroll or pinch to zoom';
      this._container.appendChild(this._el);
      this._created = true;
    }

    // Make visible
    clearTimeout(this._hideTimer);
    // Force reflow so the transition triggers even if the element was just created
    void this._el.offsetWidth;
    this._el.classList.add('cloudimage-360-scroll-hint--visible');

    this._hideTimer = setTimeout(() => {
      this._el.classList.remove('cloudimage-360-scroll-hint--visible');
    }, AUTO_HIDE_MS);
  }

  destroy() {
    clearTimeout(this._hideTimer);
    if (this._el) {
      this._el.remove();
      this._el = null;
    }
    this._created = false;
  }
}
