/**
 * ZoomPan — CSS transform-based zoom & pan for the 360 viewer.
 * Ported from js-cloudimage-hotspot zoom/zoom-pan.ts to plain JS.
 *
 * Applies `transform: scale(z) translate(px, py)` on a viewport element.
 * Sets `--zoom` CSS variable on viewport for hotspot counter-scaling.
 *
 * Supports:
 *  - Ctrl+scroll zoom (regular scroll fires onScrollWithoutZoom)
 *  - Double-click / single-click toggle (1x ↔ 2x)
 *  - Mouse drag pan when zoomed (blocks 360 rotation via e.preventDefault)
 *  - Safari GestureEvent
 *  - Touch gestures via GestureRecognizer
 *  - Pan clamping to keep image edges at container edges
 */

import { GestureRecognizer } from './gesture-recognizer';

const ZOOM_STEP_WHEEL = 0.003; // per deltaY pixel

export class ZoomPan {
  /**
   * @param {HTMLElement} viewport — the element that gets transform: scale() translate()
   * @param {HTMLElement} container — the outer bounding box (for clamping)
   * @param {Object} opts
   * @param {number}   opts.zoomMin        — minimum zoom level (default 1)
   * @param {number}   opts.zoomMax        — maximum zoom level
   * @param {string}   opts.toggleTrigger  — 'click' | 'dblclick'
   * @param {function(level: number): void} opts.onZoom — called on zoom change
   * @param {function(): void}              opts.onScrollWithoutZoom — regular scroll intercepted
   */
  constructor(viewport, container, opts) {
    this.viewport = viewport;
    this.container = container;
    this.zoomMin = opts.zoomMin ?? 1;
    this.zoomMax = opts.zoomMax ?? 3;
    this.toggleTrigger = opts.toggleTrigger ?? 'dblclick';
    this.onZoomCb = opts.onZoom;
    this.onScrollWithoutZoom = opts.onScrollWithoutZoom;

    this._zoom = 1;
    this._panX = 0;
    this._panY = 0;
    this._enabled = true;
    this._dragging = false;
    this._dragStart = null;
    this._safariGestureScale = 1;

    this._cleanups = [];

    this._applyTransform();
    this._bindWheel();
    this._bindToggle();
    this._bindMouseDrag();
    this._bindSafariGesture();
    this._bindTouchGestures();
  }

  // ── Public API ──────────────────────────────────────────

  getZoom() {
    return this._zoom;
  }

  setZoom(level, pivotX, pivotY) {
    const clamped = Math.max(this.zoomMin, Math.min(this.zoomMax, level));
    if (clamped === this._zoom) return;

    // Adjust pan to zoom toward pivot (in container coordinates)
    if (pivotX !== undefined && pivotY !== undefined) {
      const rect = this.container.getBoundingClientRect();
      const relX = pivotX - rect.left;
      const relY = pivotY - rect.top;

      // Convert pivot to viewport-local coordinates at current zoom
      const vpX = (relX - this._panX * this._zoom) / this._zoom;
      const vpY = (relY - this._panY * this._zoom) / this._zoom;

      // After zoom change, pan so that the same viewport point stays under the pivot
      this._panX = (relX - vpX * clamped) / clamped;
      this._panY = (relY - vpY * clamped) / clamped;
    }

    this._zoom = clamped;

    // Reset pan if back to 1
    if (this._zoom <= 1) {
      this._panX = 0;
      this._panY = 0;
    } else {
      this._clampPan();
    }

    this._applyTransform();
    this.onZoomCb?.(this._zoom);
  }

  resetZoom() {
    if (this._zoom === 1) return;
    this._zoom = 1;
    this._panX = 0;
    this._panY = 0;
    this._applyTransform();
    this.onZoomCb?.(1);
  }

  pan(dx, dy) {
    if (this._zoom <= 1) return;
    this._panX += dx / this._zoom;
    this._panY += dy / this._zoom;
    this._clampPan();
    this._applyTransform();
  }

  enable() {
    this._enabled = true;
  }

  disable() {
    this._enabled = false;
  }

  destroy() {
    this._cleanups.forEach((fn) => fn());
    this._cleanups = [];
    if (this._gesture) {
      this._gesture.destroy();
      this._gesture = null;
    }
  }

  // ── Internal ────────────────────────────────────────────

  _applyTransform() {
    const z = this._zoom;
    const tx = this._panX;
    const ty = this._panY;
    this.viewport.style.transform = `scale(${z}) translate(${tx}px, ${ty}px)`;
    this.viewport.style.setProperty('--zoom', z);
  }

  _clampPan() {
    const z = this._zoom;
    if (z <= 1) {
      this._panX = 0;
      this._panY = 0;
      return;
    }

    const rect = this.container.getBoundingClientRect();
    const w = rect.width;
    const h = rect.height;

    // Viewport is scaled from origin (0,0).
    // Visible area in viewport coords: [-panX, -panX + w/z] x [-panY, -panY + h/z]
    // Viewport content is [0, w] x [0, h] (same size as container before scale).
    // Constraint: the visible window must stay within [0, w] x [0, h].
    //   -panX >= 0          →  panX <= 0
    //   -panX + w/z <= w    →  panX >= -(w - w/z)  →  panX >= -w*(1 - 1/z)
    //   similarly for Y
    const maxPanX = 0;
    const minPanX = -w * (1 - 1 / z) / z;
    // Actually let me re-derive: transform is scale(z) translate(tx, ty).
    // A point (px, py) in the viewport maps to screen position (z*(px+tx), z*(py+ty)).
    // Top-left corner of viewport (0,0) maps to (z*tx, z*ty).
    // We want this to be <= (0,0) so content doesn't leave the top-left.
    //   z*tx <= 0  →  tx <= 0
    //   z*ty <= 0  →  ty <= 0
    // Bottom-right corner of viewport (w,h) maps to (z*(w+tx), z*(h+ty)).
    // We want this to be >= (w, h) so content doesn't leave bottom-right.
    //   z*(w+tx) >= w  →  tx >= w/z - w  →  tx >= -w*(z-1)/z
    //   z*(h+ty) >= h  →  ty >= -h*(z-1)/z

    const minX = -w * (z - 1) / z;
    const maxX = 0;
    const minY = -h * (z - 1) / z;
    const maxY = 0;

    this._panX = Math.max(minX, Math.min(maxX, this._panX));
    this._panY = Math.max(minY, Math.min(maxY, this._panY));
  }

  // ── Ctrl + Scroll Wheel ─────────────────────────────────

  _bindWheel() {
    const handler = (e) => {
      if (!this._enabled) return;

      if (e.ctrlKey || e.metaKey) {
        e.preventDefault();
        const delta = -e.deltaY * ZOOM_STEP_WHEEL;
        this.setZoom(this._zoom + delta, e.clientX, e.clientY);
      } else if (this._zoom <= 1) {
        // Regular scroll without zoom — pass through to page, but notify
        this.onScrollWithoutZoom?.();
      } else {
        // When zoomed, convert scroll into pan
        e.preventDefault();
        this.pan(-e.deltaX, -e.deltaY);
      }
    };

    this.viewport.addEventListener('wheel', handler, { passive: false });
    this._cleanups.push(() => this.viewport.removeEventListener('wheel', handler));
  }

  // ── Click / DblClick Toggle ─────────────────────────────

  _bindToggle() {
    const eventName = this.toggleTrigger === 'click' ? 'click' : 'dblclick';

    const handler = (e) => {
      if (!this._enabled) return;

      // Don't handle toggle on interactive elements
      if (e.target.closest?.('.cloudimage-360-button, .cloudimage-360-hotspot, .cloudimage-360-hotspot-timeline-dot, .cloudimage-360-zoom-controls')) return;

      if (this._zoom > 1) {
        this.resetZoom();
      } else {
        // Toggle to 2x (capped by zoomMax)
        const targetZoom = Math.min(2, this.zoomMax);
        this.setZoom(targetZoom, e.clientX, e.clientY);
      }
    };

    this.viewport.addEventListener(eventName, handler);
    this._cleanups.push(() => this.viewport.removeEventListener(eventName, handler));
  }

  // ── Mouse Drag Pan ──────────────────────────────────────

  _bindMouseDrag() {
    const onDown = (e) => {
      if (!this._enabled || this._zoom <= 1) return;
      // Only left button
      if (e.button !== 0) return;
      // Don't intercept interactive element clicks
      if (e.target.closest?.('.cloudimage-360-button, .cloudimage-360-hotspot, .cloudimage-360-hotspot-timeline-dot, .cloudimage-360-zoom-controls')) return;

      e.preventDefault();
      this._dragging = true;
      this._dragStart = { x: e.clientX, y: e.clientY };
      this.viewport.classList.add('cloudimage-360-viewport--dragging');
    };

    const onMove = (e) => {
      if (!this._dragging || !this._dragStart) return;
      e.preventDefault();
      const dx = e.clientX - this._dragStart.x;
      const dy = e.clientY - this._dragStart.y;
      this._dragStart = { x: e.clientX, y: e.clientY };
      this.pan(dx, dy);
    };

    const onUp = () => {
      if (!this._dragging) return;
      this._dragging = false;
      this._dragStart = null;
      this.viewport.classList.remove('cloudimage-360-viewport--dragging');
    };

    this.viewport.addEventListener('mousedown', onDown);
    document.addEventListener('mousemove', onMove);
    document.addEventListener('mouseup', onUp);

    this._cleanups.push(
      () => this.viewport.removeEventListener('mousedown', onDown),
      () => document.removeEventListener('mousemove', onMove),
      () => document.removeEventListener('mouseup', onUp),
    );
  }

  // ── Safari GestureEvent ─────────────────────────────────

  _bindSafariGesture() {
    if (typeof GestureEvent === 'undefined') return;

    const onStart = (e) => {
      if (!this._enabled) return;
      e.preventDefault();
      this._safariGestureScale = this._zoom;
    };

    const onChange = (e) => {
      if (!this._enabled) return;
      e.preventDefault();
      const newZoom = this._safariGestureScale * e.scale;
      const rect = this.container.getBoundingClientRect();
      this.setZoom(newZoom, rect.left + rect.width / 2, rect.top + rect.height / 2);
    };

    const onEnd = (e) => {
      if (!this._enabled) return;
      e.preventDefault();
    };

    this.viewport.addEventListener('gesturestart', onStart);
    this.viewport.addEventListener('gesturechange', onChange);
    this.viewport.addEventListener('gestureend', onEnd);

    this._cleanups.push(
      () => this.viewport.removeEventListener('gesturestart', onStart),
      () => this.viewport.removeEventListener('gesturechange', onChange),
      () => this.viewport.removeEventListener('gestureend', onEnd),
    );
  }

  // ── Touch Gestures (via GestureRecognizer) ──────────────

  _bindTouchGestures() {
    this._gesture = new GestureRecognizer(this.viewport, {
      onPinch: (scaleDelta, cx, cy) => {
        if (!this._enabled) return;
        this.setZoom(this._zoom * scaleDelta, cx, cy);
      },
      onPinchEnd: () => {
        // Nothing extra needed — zoom state is already applied
      },
      onPan: (dx, dy) => {
        if (!this._enabled) return;
        this.pan(dx, dy);
      },
      onPanEnd: () => {
        // Nothing extra needed
      },
      onDoubleTap: (x, y) => {
        if (!this._enabled) return;
        if (this._zoom > 1) {
          this.resetZoom();
        } else {
          const targetZoom = Math.min(2, this.zoomMax);
          this.setZoom(targetZoom, x, y);
        }
      },
      isPanEnabled: () => this._zoom > 1,
    });
  }
}
