/**
 * Zoom Controls — +/−/reset buttons for the 360 viewer.
 * Ported from js-cloudimage-hotspot zoom/controls.ts to plain JS.
 */

const ZOOM_IN_SVG = `<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><line x1="12" y1="5" x2="12" y2="19"/><line x1="5" y1="12" x2="19" y2="12"/></svg>`;

const ZOOM_OUT_SVG = `<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><line x1="5" y1="12" x2="19" y2="12"/></svg>`;

const RESET_SVG = `<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><polyline points="1 4 1 10 7 10"/><path d="M3.51 15a9 9 0 1 0 2.13-9.36L1 10"/></svg>`;

/**
 * @param {HTMLElement} container — element to append controls into
 * @param {import('./zoom-pan').ZoomPan} zoomPan
 * @param {Object} opts
 * @param {number} opts.zoomStep   — zoom increment per button press (default 0.5)
 * @param {string} opts.position   — 'bottom-left' | 'bottom-right' | 'top-left' | 'top-right'
 * @returns {{ element: HTMLElement, update: function, destroy: function }}
 */
export function createZoomControls(container, zoomPan, opts = {}) {
  const zoomStep = opts.zoomStep ?? 0.5;
  const position = opts.position ?? 'bottom-left';

  const wrapper = document.createElement('div');
  wrapper.className = 'cloudimage-360-zoom-controls';
  wrapper.setAttribute('data-position', position);
  wrapper.setAttribute('role', 'group');
  wrapper.setAttribute('aria-label', 'Zoom controls');

  const btnIn = _createButton('Zoom in', ZOOM_IN_SVG);
  const btnOut = _createButton('Zoom out', ZOOM_OUT_SVG);
  const btnReset = _createButton('Reset zoom', RESET_SVG);

  btnIn.addEventListener('click', (e) => {
    e.stopPropagation();
    const current = zoomPan.getZoom();
    const rect = container.getBoundingClientRect();
    zoomPan.setZoom(current + zoomStep, rect.left + rect.width / 2, rect.top + rect.height / 2);
    update();
  });

  btnOut.addEventListener('click', (e) => {
    e.stopPropagation();
    const current = zoomPan.getZoom();
    const rect = container.getBoundingClientRect();
    zoomPan.setZoom(current - zoomStep, rect.left + rect.width / 2, rect.top + rect.height / 2);
    update();
  });

  btnReset.addEventListener('click', (e) => {
    e.stopPropagation();
    zoomPan.resetZoom();
    update();
  });

  wrapper.appendChild(btnIn);
  wrapper.appendChild(btnOut);
  wrapper.appendChild(btnReset);
  container.appendChild(wrapper);

  function update() {
    const z = zoomPan.getZoom();
    btnIn.disabled = z >= zoomPan.zoomMax;
    btnOut.disabled = z <= zoomPan.zoomMin;
    btnReset.disabled = z <= zoomPan.zoomMin;
  }

  update();

  function destroy() {
    wrapper.remove();
  }

  return { element: wrapper, update, destroy };
}

function _createButton(label, svgHTML) {
  const btn = document.createElement('button');
  btn.type = 'button';
  btn.className = 'cloudimage-360-button cloudimage-360-zoom-control-button';
  btn.setAttribute('aria-label', label);
  btn.innerHTML = svgHTML;
  return btn;
}
