/**
 * GestureRecognizer — touch gesture detection for zoom/pan.
 * Ported from js-cloudimage-hotspot zoom/gestures.ts to plain JS.
 *
 * Recognises:
 *  - Pinch (2-finger) with scale delta
 *  - Pan (1-finger when zoomed)
 *  - Double-tap (with wasPinching guard to suppress accidental taps)
 */

const DOUBLE_TAP_DELAY = 300; // ms
const PINCH_COOLDOWN = 350; // ms after pinch ends before taps are recognised

export class GestureRecognizer {
  /**
   * @param {HTMLElement} element
   * @param {Object} callbacks
   * @param {function(scaleDelta: number, centerX: number, centerY: number): void} callbacks.onPinch
   * @param {function(): void} callbacks.onPinchEnd
   * @param {function(dx: number, dy: number): void} callbacks.onPan
   * @param {function(): void} callbacks.onPanEnd
   * @param {function(x: number, y: number): void} callbacks.onDoubleTap
   * @param {function(): boolean} callbacks.isPanEnabled — return true when zoom > 1
   */
  constructor(element, callbacks) {
    this.el = element;
    this.cb = callbacks;
    this.cleanups = [];

    // Internal state
    this._lastPinchDist = 0;
    this._pinching = false;
    this._wasPinching = false;
    this._pinchEndTime = 0;
    this._lastTapTime = 0;
    this._panStart = null;

    this._onTouchStart = this._onTouchStart.bind(this);
    this._onTouchMove = this._onTouchMove.bind(this);
    this._onTouchEnd = this._onTouchEnd.bind(this);

    element.addEventListener('touchstart', this._onTouchStart, { passive: false });
    element.addEventListener('touchmove', this._onTouchMove, { passive: false });
    element.addEventListener('touchend', this._onTouchEnd);
    element.addEventListener('touchcancel', this._onTouchEnd);

    this.cleanups.push(
      () => element.removeEventListener('touchstart', this._onTouchStart),
      () => element.removeEventListener('touchmove', this._onTouchMove),
      () => element.removeEventListener('touchend', this._onTouchEnd),
      () => element.removeEventListener('touchcancel', this._onTouchEnd),
    );
  }

  _dist(t1, t2) {
    const dx = t1.clientX - t2.clientX;
    const dy = t1.clientY - t2.clientY;
    return Math.sqrt(dx * dx + dy * dy);
  }

  _center(t1, t2) {
    return {
      x: (t1.clientX + t2.clientX) / 2,
      y: (t1.clientY + t2.clientY) / 2,
    };
  }

  _onTouchStart(e) {
    if (e.touches.length === 2) {
      e.preventDefault();
      this._pinching = true;
      this._lastPinchDist = this._dist(e.touches[0], e.touches[1]);
      return;
    }

    // 1-finger: start pan or potential double-tap
    if (e.touches.length === 1) {
      const isPanEnabled = this.cb.isPanEnabled?.() ?? false;
      if (isPanEnabled) {
        e.preventDefault();
        this._panStart = { x: e.touches[0].clientX, y: e.touches[0].clientY };
      }
    }
  }

  _onTouchMove(e) {
    // Pinch
    if (this._pinching && e.touches.length === 2) {
      e.preventDefault();
      const dist = this._dist(e.touches[0], e.touches[1]);
      if (this._lastPinchDist > 0) {
        const scaleDelta = dist / this._lastPinchDist;
        const c = this._center(e.touches[0], e.touches[1]);
        this.cb.onPinch?.(scaleDelta, c.x, c.y);
      }
      this._lastPinchDist = dist;
      return;
    }

    // Pan (1-finger when zoomed)
    if (this._panStart && e.touches.length === 1) {
      e.preventDefault();
      const dx = e.touches[0].clientX - this._panStart.x;
      const dy = e.touches[0].clientY - this._panStart.y;
      this._panStart = { x: e.touches[0].clientX, y: e.touches[0].clientY };
      this.cb.onPan?.(dx, dy);
    }
  }

  _onTouchEnd(e) {
    // Pinch end
    if (this._pinching && (!e.touches || e.touches.length < 2)) {
      this._pinching = false;
      this._wasPinching = true;
      this._pinchEndTime = Date.now();
      this._lastPinchDist = 0;
      this.cb.onPinchEnd?.();
      return;
    }

    // Pan end
    if (this._panStart && (!e.touches || e.touches.length === 0)) {
      this._panStart = null;
      this.cb.onPanEnd?.();
    }

    // Double-tap detection (only when no fingers remain)
    if (!e.touches || e.touches.length === 0) {
      // Suppress taps right after pinch
      if (this._wasPinching && Date.now() - this._pinchEndTime < PINCH_COOLDOWN) {
        this._wasPinching = false;
        return;
      }
      this._wasPinching = false;

      const now = Date.now();
      if (now - this._lastTapTime < DOUBLE_TAP_DELAY) {
        this._lastTapTime = 0;
        const touch = e.changedTouches?.[0];
        if (touch) {
          this.cb.onDoubleTap?.(touch.clientX, touch.clientY);
        }
      } else {
        this._lastTapTime = now;
      }
    }
  }

  destroy() {
    this.cleanups.forEach((fn) => fn());
    this.cleanups = [];
  }
}
